import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';


import { registroPDV } from '../modelo_registroPDV/registroPDV';


//productos
import { productov } from '../modelo_producto/modelo_productov';


@Component({
  selector: 'app-registro-pedi-dia-v',
  templateUrl: './registro-pedi-dia-v.page.html',
  styleUrls: ['./registro-pedi-dia-v.page.scss'],
})
export class RegistroPediDiaVPage implements OnInit {

  objeto:registroPDV = { Des_de_alimnentos:"", Unidad_de_Media:"", Gr_ML_Desayuno:"", Gr_ML_Sopa:"", Gr_ML_Segundo:"", Gr_ML_Cena:""}
  
  pruebas: registroPDV[] = [];
  url = 'http://localhost/proG/server_api/crear_registroPDV.php';
  url2 = 'http://localhost/proG/server_api/menu_registroPDV.php';
 
  disabledButton;



  objeto1:productov = { tipo:"", nombreali:"", cantidad_pro:"", cantidad_min:"", cantidad_med:"", cantidad_max:""}

  pruebas1: productov[] = [];
  url3 = 'http://localhost/proG/server_api/menu_productov.php';


  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders

  ) {
    this.listarRegistroPDV();
    this.listarRegistroPA();
   }

  ngOnInit() {
  }

  agregarRegistroPDV(obj :registroPDV): Observable<registroPDV>{
    return this.http.post<registroPDV>(this.url, JSON.stringify(obj))
  }

  async agregarNU() {
    this.agregarRegistroPDV(this.objeto)
    .subscribe((resp) =>{
      this.objeto.Des_de_alimnentos='';
      this.objeto.Unidad_de_Media='';
      this.objeto.Gr_ML_Desayuno='';
      this.objeto.Gr_ML_Sopa='';
      this.objeto.Gr_ML_Segundo='';
      this.objeto.Gr_ML_Cena='';
      this.listarRegistroPDV();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }


  getRegistroPDV(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarRegistroPDV(){
    this.pruebas = [];
    this.getRegistroPDV().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }


  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_PDV',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPDV();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }


  abrirCrud(a){
    this.router.navigate(['/mod-pdv/'+a]);
  }

  
  getRegistroPA(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroPA(){
    this.pruebas1 = [];
    this.getRegistroPA().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas1.push(res[i]);
      }
    })
  }




}
